.signup{
    min-height: 100vh;
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../../black-abstract-dark-1024x1024-9729.jpg");
    background-size: cover;
    padding: 20px;
    box-sizing: border-box;
    z-index: 30;
    gap: 10px;
    top: 0;
    margin-top: 0;
}
::placeholder {
    color: #cfcfcf; 
  }
  
  input:-moz-placeholder {
    color: #cfcfcf;
  }
  
  input::-webkit-input-placeholder {
    color: #cfcfcf;
  }
.cancelbutton{
    background: transparent;
    position: absolute;
    right: 10px;
    top:   10px;
    border: none;
    font-size:25px;
    color: white;
}
.cancelbutton:hover{
    background: red;
}

 .signupform{
    zoom: 0.8;
    position: relative;
    width: 400px;
    max-width: 400px;
    min-height: 400px;
    border: 2px solid white;
    border-radius: 15px;
    box-sizing: border-box;
    padding: 20px;
    color: white;
    backdrop-filter: blur(10px);
    background: rgba(0, 0, 0, 0.244);
}
.signupform form{
    display: flex;
    flex-direction: column;
    justify-content: center;

}


 .signupform form label{
    font-weight: 600;
    font-size: large;
    color: white;
}

 .signupform form input{
    height: 30px;
    border: 2px solid white;
    border-top: none;
    border-right: none;
    border-left: none;
    margin-bottom: 30px;
    margin-top: 5px;
    font-size: 20px;
    text-indent: 20px;
    background: transparent;
    outline: none;
    color: white;
}

 .signupform >form input[type="submit"]{
    background: rgb(0, 145, 255);
    color: white;
    text-indent: 0px;
    border: none;
    border-radius: 10px;
    cursor: pointer;

}
.signup_pt_btn{
    height: 30px;
    position: absolute;
    color: white;
    border: none;
    border-left: 1px solid white;
    border-right: 1px solid white;
    top: 200px;
    right: 20px;
    padding:0px 10px 0px 10px ;
    background-color: transparent;
    font-size: large;
    border-radius: 10px;
    cursor: pointer;
}
.signup_pt_btn:hover{
    background: rgb(0, 145, 255);
    color: white;
}

 .signup .signinbtn{
    height: 30px;
    width: 100%;
    border: none;
    margin-bottom: 30px;
    margin-top: 5px;
    font-size: 20px;
    text-indent: 20px;
    border-radius: 10px;
    background: transparent;
    cursor: pointer;
    text-indent: 0;
    color: white;
}
 .signupform .signinbtn:hover{
    background: rgb(0, 145, 255);
    color: white;

}
@media screen and (width<900px) {
    .signup{
        flex-direction: column;
        gap: 100px;
    }
    
}
@media screen and (width<600px) {
    .signup{
        flex-direction: column;
        gap: 100px;
    }
    
}