body{
  margin: 0px;
  padding: 0;
  background: url("./black-abstract-dark-1024x1024-9729.jpg");
  background-size: cover;
  overflow-x: hidden;
  user-select: none;
}
::-webkit-scrollbar{
  width: 1px;
}
.taskmanager{
  width: 100vw;
  height: 50px;
}
.signupbutton{
  height: 20px;
  width: 50px;
  border: 2px solid white;
  border-radius: 10px;
  padding: 3px 10px;
  position: absolute;
  display: flex;
  justify-content: center;
  align-items: center;
  top: 15px;
  right: 20px;
}
.signupbutton:hover{
  background: rgb(0, 136, 255);
}
.menubutton{
  height: 30px;
  aspect-ratio: 1;
  position: absolute;
  top: 20px;
  left: 20px;
  background: white;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  display: none;
}
.taskmanager h2{
  color: skyblue;
  text-align: center;

}
.taskarea{
  display: flex;
}
.taskmanagerleft{
  text-align: center;
  overflow-y: scroll;
  height:calc( 100vh - 90px);
  margin-left: 8px;
  width: 40vw;
  border: 1px solid;
  padding: 0px 10px 60px 10px ;
  box-sizing: border-box;
  position: relative;
  color: white;
  border-radius: 10px;
}
#refresh{
  height: 30px;
  border-radius: 15px;
  position: absolute;
  top: 17px;
  right: 10px;
  background: transparent;
  border: 2px solid white;
  color: white;
}
#refresh:hover{
  background: rgb(0, 136, 255);
}
.addtask{
  height: 40px;
  border-radius: 25px;
  width: calc(100% - 20px);
  position: absolute;
  box-sizing: border-box;
  border: 2px solid white;
  background: transparent;
  display: flex;
  justify-content: center;
  align-items: center;
}
.addtask:hover{
  background: green;
}
.taskmanagerright{
  width: 70vw;
  padding: 10px;
  color: white;
}

/* tasklist */
.tasklisttask{
  border: 2px solid white;
  display: flex;
  align-items: center;
  height: 50px;
  border-radius: 25px;
  padding-left: 10px;
  width: 100%;
  margin-bottom: 10px;
  justify-content: space-evenly;
  box-sizing: border-box;
  overflow: hidden;
}
.tasklisttask h3{
  width: 100%;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
  color: white;
}
.tasklisttask button{
  border: none;
  min-height: 48px;
  min-width: 60px;
  border-radius: 25px 0px 0px 25px;
}
.tasklisttask button:nth-child(3){
  border-radius: 0px 25px 25px 0px;
  border-left: 1px solid black;
}
.tasklisttask button:nth-child(3):hover{
  background: red;
  color: white;
  width: 60px;
}

/* showtask */
.secondarydetails{
  display: flex;
  justify-content: space-around;
}
.complete{
  position: absolute;
  bottom: 20px;
  right: 10px;
  background: green;
  padding: 10px;
  border-radius:20px ;
}
/* create task */
.createtask{
  display: grid;
}
.createtask button{
  max-width: 200px;
  min-width: 200px;
  height: 30px;
  border-radius: 15px;
  justify-self: center;
  border: none;
}
.createtask input{
  min-width:100%;
  background: transparent;
  color: white;
  height: 30px;
  justify-self: center;
  margin-top: 10px;
  border: none;
  border-bottom: 2px solid white;
  outline: none;
}
input[type="date"]::-webkit-calendar-picker-indicator {
  filter: invert(1); 
  color: #ffffff;
}
.createtask button:hover{
  background: green;
  color: white;
}


/*  */

@media screen and (width<800px) {
  .menubutton{
    display: flex;
  }
  .taskmanagerleft{
    transform: translateX(-40vw);
    transition: 0.3s;
    width: 0;
    
  }
  .taskmanagerright{
    width: 100vw;
    transition: 0.3s;
  }
}
@media screen and (width<550px) {
  .menubutton{
    display: flex;
  }
  .secondarydetails{
    display: flex;
    flex-direction: column;
  }
}

/* admin home */
.adminhome{
  min-height: 100vh;
  position: absolute;
  width: 100%;
  background: url("./black-abstract-dark-1024x1024-9729.jpg");
  background-size: cover;
  box-sizing: border-box;
  padding: 20px;
  color: white;
  z-index: 30;
  top: 0;
}
.adminhome h2{
  text-align: center;
}
.userlist{
  display: flex;
  flex-direction: column;
  gap: 10px;
  
}

/* allusers */
.Allusers{
  height: 50px;
  border-radius: 25px;
  width: 100%;
  border: 2px solid white;
  box-sizing: border-box;
  display: flex;
  justify-content: space-around;
  align-items: center;
}

/* user */
.user{
  min-height: 100vh;
  position: absolute;
  top: 0;
  width: 100%;
  background: url("./black-abstract-dark-1024x1024-9729.jpg");
  background-size: cover;
  box-sizing: border-box;
  color: white;
  z-index: 30;
  display: flex;
  
}
.userleft{
  box-sizing: border-box;
  width: 50%;
  min-height: 100vh;
  position: relative;
}
.userlefttop{
  height: 50vh;
  padding: 20px 50px;
  box-sizing: border-box;
}

.userleftbottom{
  height: 50vh;
 display: flex;
 justify-content: center;
 align-items: center;
}
.piechart{
  position: relative;
  height: 50vh;
  aspect-ratio: 1;
  bottom: 30px
}
svg{
  position: absolute;
  color: white;
}
/*  */
.userright{
  box-sizing: border-box;
  width: 50%;
  height: fit-content;
  overflow-y: scroll;
  position: relative;
}
.userright h2{
  text-align: center;
  width: 100%;
  background:rgba(39, 39, 39, 0.721);
  position: absolute;
  margin-top: 0;
  padding:20px 0;
  top: 0;
}
.admintasks{
  padding: 80px 10px;
}
@media screen and (width<660px) {
  .user{
    flex-direction: column;
    align-items: center;
  }
  .userleft{
    width: 100%;
  }
  .userright{
    width: 100%;
  }
}
/* Admintasklist */
.Admintasklist {
  border-radius: 40px;
  padding: 0px 40px;
  max-height: fit-content;
  overflow-y: hidden;
  margin-bottom: 10px;
  
}
.Admintasklist h3{
  text-align: center;
  color: white;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}
