.signin{
    min-height: 100vh;
    position: absolute;
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
    background: url("../../black-abstract-dark-1024x1024-9729.jpg");
    background-size: cover;
    padding: 20px;
    box-sizing: border-box;
    z-index: 30;
    top: 0;
    margin-top: 0;
    
}
.signin_pt_btn{
    height: 30px;
    position: absolute;
    color: white;
    top: 110px;
    right: 20px;
    padding:0px 10px 0px 10px ;
    background-color: transparent;
    border: none;
    border-left: 1px solid white;
    border-right: 1px solid white;
    font-size: large;
    border-radius: 10px;
    cursor: pointer;
}
.signin_pt_btn:hover{
    background: rgb(0, 145, 255);
    color: white;
}

.signinbtn{
    height: 30px;
    width: 100%;
    border: none;
    margin-bottom: 30px;
    margin-top: 5px;
    font-size: 20px;
    text-indent: 20px;
    border-radius: 10px;
    background: transparent;
    cursor: pointer;
    text-indent: 0;
    color: white;
}
.signinbtn:hover{
    background: rgb(0, 145, 255);
    color: white;

}